import React from 'react'
import { Layout } from '@layout'

const PostGS = ({
  data: { postGS },
  ...props
}) => {

  const { pageContext: { services = {}, siteUrl } = {} } = props

  return (
    <Layout {...props}>
      {postGS?.title || 'Post GS'}
    </Layout>
  )
}

export default PostGS
